import React, { Component } from 'react';
import './LogBook.css'
import LogItemList from './LogItemList';
import { Link } from 'react-router-dom';



class logBook extends Component {
    state = {
        completedTodo: []
    }

    fetchData() {
        fetch("http://localhost:4000/completedList", {
            method: "GET"
        })
            .then((res) => res.json())
            .then((data) => this.setState({
                completedTodo: data,
            }))
    }

    componentDidMount() {
        this.fetchData();
    }

    render() {
        const { completedTodo } = this.state;
        return (
            <div>
                <div>
                    <header>
                        <h1>완료 목록</h1>
                    </header>
                    <main>
                        <div>
                            <LogItemList completedTodo={completedTodo} />
                        </div>

                    </main>
                    <Link to="/TodoTemplate"> <footer>Todo List</footer> </Link>
                </div>
            </div>
        )
    }
}

export default logBook;
