import React, { Component } from 'react';
import Logitem from './LogItem'

class LogItemList extends Component {
    render() {
        const { completedTodo } = this.props;
        return (
            <>
                <div className="log-list">
                    {completedTodo.map((completed, i) => <Logitem key={i} completed={completed} />)}
                </div>
            </>
        )
    }
}

export default LogItemList;