import React, { Component } from 'react';
import TodoTemplate from './components/TodoTemplate'
import LogBook from './components/LogBook';
import { Switch, Route } from 'react-router-dom';
// import { testData } from './testData.json';
import axios from 'axios';

class App extends Component {

  state = {
    input: '',
    todos: [],
    value: false
  };


  fetchData() {
    fetch("http://localhost:4000/list", {
      method: "GET"
    })
      .then((res) => res.json())
      .then((data) => this.setState({
        todos: data,
      }))
  }

  componentDidMount() {
    this.fetchData();
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevState.input !== this.state.input) {
      console.log("업데이트")
      this.fetchData()
    }

  }

  //그냥 쓰니까 무한 루프


  handleChange = (e) => {
    this.setState({
      input: e.target.value
    })
  };

  handleCreate = () => {
    const { input, todos, } = this.state
    axios
      .post("http://localhost:4000/create", {
        text: input
      })
      .then(res => console.log("여기 데티어", res))
      .then((res) => {
        this.setState({
          input: '',
          todos: todos.concat({
            id: this.id++,
            text: input
          })
        })
      })

  };

  hnadleKeyPres = (e) => {
    if (e.key === 'Enter') {
      this.handleCreate();
    }
  };

  handleCompleted = (id) => {
    const { todos } = this.state;
    console.log("기 아이디", id)
    axios
      .post("http://localhost:4000/completed", {
        text: id
      })
    axios
      .post("http://localhost:4000/remove", {
        text: id
      })
      .then(this.setState({
        todos: todos.filter((todo) => todo.id !== id)
      }))


    // this.setState({ // todo 테이블 삭제 하는 API 실행 + 완료 테이블에 추가하는 API 실행
    //   todos: todos.filter((todo) => todo.id !== id),
    //   completedTodo: completedTodo.concat(todos.filter((todo) => todo.id === id))
    // })
  }

  render() {
    const { input, todos } = this.state;
    const { handleChange, handleCreate, hnadleKeyPres, handleCompleted } = this
    return (
      <div>
        <Switch>
          <Route
            path="/LogBook"
            render={() => <LogBook
            />}
          />
          <Route
            path="/"
            render={() => <TodoTemplate
              value={input}
              onChange={handleChange}
              onCreate={handleCreate}
              onKeyPress={hnadleKeyPres}
              todos={todos} completed={handleCompleted}
            />}
          />
        </Switch>
      </div>
    );
  }
}

export default App;