import React, { Component } from 'react';
import './TodoTemplate.css';
import TodoInput from './TodoInput'
import TodoItemList from './TodoItemList'
import { Link } from 'react-router-dom';
import { testData } from '../testData.json';



class TodoTemplate extends Component {


    render() {
        const { value, onChange, onCreate, onKeyPress, todos, completed } = this.props;

        return (
            <div>
                <header>
                    <h1>Todo List</h1>
                    <TodoInput
                        value={value}
                        onChange={onChange}
                        onCreate={onCreate}
                        onKeyPress={onKeyPress}
                    />
                </header>
                <main>
                    <div>
                        <TodoItemList todos={todos} completed={completed} />
                    </div>

                </main>
                <Link to="/LogBook" todos={todos}><footer>LogBook</footer></Link>
            </div>
        )
    }
}

export default TodoTemplate;